import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { LogService } from 'src/app/service/log.service';

@Component({
  selector: 'app-overview',
  templateUrl: './overview.component.html',
  styleUrls: ['./overview.component.scss'],
  providers: [ LogService ]
})
export class OverviewComponent implements OnInit, OnDestroy {

  private countInfo;
  private logOverview;
  private httpSubscription: Subscription = new Subscription();

  constructor(private logService: LogService) { }

  ngOnInit() {
    this.GetCountTotal();
    this.GetLogOverview();
  }

  ngOnDestroy(){
    this.httpSubscription.unsubscribe();
  }

  private GetCountTotal(){
    this.httpSubscription.add(this.logService.GetCountTotal()
    .subscribe(result => this.countInfo = result[0]));
  }

  private GetLogOverview(){
    this.httpSubscription.add(this.logService.GetLogOverview()
    .subscribe(result => this.logOverview = result));
  }
}
