import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/service/user.service';
import { RoleService } from 'src/app/service/role.service';

declare var $: any;
@Component({
  selector: 'app-user-main',
  templateUrl: './user-main.component.html',
  styleUrls: ['./user-main.component.scss']
})
export class UserMainComponent implements OnInit {
  constructor(private _userService: UserService, private _roleService: RoleService) { }
  users;
  usernamePicked;
  userIdPicked;
  roles;
  newUser;
  getUserPicked(usn, uid) {
    this.usernamePicked = usn;
    this.userIdPicked = uid;
  }
  ngOnInit() {
    this.GetAllUsers();
    this._roleService.GetAllRoles().subscribe(x => this.roles = x);
  }
  findIdByNameRole(roleNameSelected): number {
    var t = -1;
    this.roles.forEach(e => {
      if (e.RoleName == roleNameSelected) {
        t = e.RoleId;
      }
    });
    return t;
  }
  onSubmit(formAdd) {
    this.newUser = formAdd.value;
    this.newUser.RoleId = this.findIdByNameRole(this.newUser.RoleId);
    this.newUser.Gender = this.newUser.Gender === "Male" ? true : false;
    this.AddUsers();
  }
  private AddUsers() {
    this._userService.AddUser(this.newUser).subscribe
      (success => {
        alert("You add users successful!");
        window.location.reload();
      }, error => alert(error));
  }
  private GetAllUsers() {
    this._userService.GetAllUsers().subscribe(data => this.users = data);
  }
  public DeleteUser(id) {
    this._userService.DeleteUser(id).subscribe(
      success => {
        $("#" + id).hide();
        alert("You delete users successful!");
        window.location.reload();
      }, error => alert(error));
  }
}
