import { Component, OnInit, OnDestroy } from '@angular/core';
import { LogService } from 'src/app/service/log.service';
import { AngularEditorConfig } from '@kolkov/angular-editor';
import { Subscription, Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
declare var $: any;

@Component({
  selector: 'app-log-main',
  templateUrl: './log-main.component.html',
  styleUrls: ['./log-main.component.scss'],
  providers: [LogService]
})
export class LogMainComponent implements OnInit, OnDestroy {
  public LogIdPicked;
  private logTypeNgModel = 'Info';
  private statusNgModel = 'Open';
  private descriptionNgModel;
  private logs;
  private filter;
  private filteredOption;
  private p: number = 1;
  private myControl = new FormControl();
  private userID = localStorage.getItem('userID');
  private httpSubscription: Subscription = new Subscription();

  constructor(private logService: LogService) {
  }

  ngOnInit() {
    this.GetAllLogs();
    this.filteredOption = this.myControl.valueChanges
      .pipe(
        startWith(''),
        map(value => value.length > 0 ? this._filter(value.toString()) : []));
  }

  ngOnDestroy() {
    this.httpSubscription.unsubscribe();
  }
  private setId(id){
    this.LogIdPicked=id;
  }
  private _filter(value: string): string {
    const filterValue = value.toLowerCase();
    return this.logs.filter(result => result['LogName'].toLowerCase().includes(filterValue));
  }

  private GetAllLogs() {
    this.httpSubscription.add(this.logService.GetAllLogs()
      .subscribe(result => this.logs = result));
  }

  private SearchLog() {
    if(this.myControl.value === '')
    {
      this.GetAllLogs();
    }
    else
    {
      this.httpSubscription.add(this.logService.SearchLog(this.myControl.value)
      .subscribe(result => this.logs = result));
    } 
  }

  private OnSubmit(formAddLog) {
    this.httpSubscription.add(this.logService.CreateLog(this.userID, formAddLog.value)
      .subscribe(result => {
        this.GetAllLogs();
        $('#logName').val('');
        this.logTypeNgModel = 'Info';
        this.statusNgModel = 'Open'
        this.descriptionNgModel = '';
        $('#CreateLogModal').modal('hide');
      }));
  }

  private DeleteLog(logID) {
    this.httpSubscription.add(
      this.logService.DeleteLog(logID)
        .subscribe(success=>{
          $('#'+logID).hide();
        }));
  }

  editorConfig: AngularEditorConfig = {
    editable: true,
    height: '25px'
  };
}