import { Component, OnInit } from '@angular/core';
import { RoleService } from 'src/app/service/role.service';
import { Router, ActivatedRoute } from '@angular/router';
declare var $: any;
@Component({
  selector: 'app-role-details',
  templateUrl: './role-details.component.html',
  styleUrls: ['./role-details.component.scss']
})
export class RoleDetailsComponent implements OnInit {
  rid: number;
  constructor(private roleService: RoleService, private router: ActivatedRoute) {
  }
  roleDetails;
  userRoles;
  newRole;
  permissionValue;
  tempPermissionValue;
  viewPermission;
  createPermission;
  modifyPermission;

  ngOnInit() {
    this.router.params.subscribe(params => {
      this.rid = +params['id'];
    });
    this.GetRoledata(this.rid);
  }
  GetRoledata(id) {
    this.roleService.GetRoleDetails(id).subscribe(x => {
      this.roleDetails = x[0];
      this.tempPermissionValue = x[0].Permissions;
      this.userRoles = x;
    });
  }
  onSubmit(formEditRole, rid) {
    this.newRole = formEditRole.value;
    this.newRole.Permissions = this.GetPermission();
    this.newRole.RoleId = rid;
    this.UpdateRoles();
  }
  private GetPermission() {
    var permission = 0;
    $('#checkboxes input:checked').each(function () {
      permission += parseInt($(this).attr('name'));
    });
    return permission;
  }
  private UpdateRoles() {
    this.roleService.UpdateRole(this.newRole).subscribe
      (success => {
        alert("You updates roles successful!");
        window.location.reload();
      }, error => alert(error));
  }

  private InitialPermissionValue() {
    this.permissionValue = this.tempPermissionValue;
    this.CheckedByPermission();
  }

  private CheckedByPermission() {
    if (this.permissionValue === 1 || this.permissionValue === 3 || this.permissionValue === 5 || this.permissionValue === 7) {
      this.viewPermission = true;
    }
    if (this.permissionValue === 2 || this.permissionValue === 3 || this.permissionValue === 6 || this.permissionValue === 7) {
      this.createPermission = true;
    }
    if (this.permissionValue === 4 || this.permissionValue === 5 || this.permissionValue === 6 || this.permissionValue === 7) {
      this.modifyPermission = true;
    }
  }
}
