import { RoleService } from 'src/app/service/role.service';
import { Component, OnInit } from '@angular/core';
declare var $: any;
@Component({
  selector: 'app-log',
  templateUrl: './role.component.html',
  styleUrls: ['./role.component.css']
})
export class RoleComponent implements OnInit {
  constructor(private _roleService: RoleService) { }
  newRole;
  roles;
  rolenamePicked;
  roleIdPicked;
  getRolePicked(rn, rid) {
    this.rolenamePicked = rn;
    this.roleIdPicked = rid;
  }
  ngOnInit() {
    this.GetAllRoles();
  }
  private GetAllRoles() {
    this._roleService.GetAllRoles().subscribe(data => this.roles = data);
  }
  onSubmit(formAddRole) {
    this.newRole = formAddRole.value;
    this.AddRoles()
  }
  private AddRoles() {
    this.newRole.Permissions = this.GetPermission();
    this._roleService.AddRole(this.newRole).subscribe
      (success => {
        alert("You add roles successful!");
        window.location.reload();
      }, error => alert(error));
  }
  public DeleteRole(id) {
    this._roleService.DeleteRole(id).subscribe(
      success => {
        $("#" + id).hide();
        alert("You delete roles successful!");
        window.location.reload();
      }, error => alert(error));
  }
  private GetPermission() {
    var permission = 0;
    $('#checkboxes input:checked').each(function () {
      permission += parseInt($(this).attr('name'));
    });
    return permission;
  }
}
