import { Component, OnInit } from '@angular/core';
import { LogService } from 'src/app/service/log.service';
import { UserLogService } from 'src/app/service/userLog.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
  providers: [LogService]

})
export class DashboardComponent implements OnInit {
    //count the number of role, user and log
    roleCount:number;
    userCount:number;
    logCount:number;
    newsCount:number=0;

    //All Data of logs
    Logs;
    //Percent of Log Data
    openPercent:number;
    processPercent:number;
    donePercent:number;
    //Daily log
    Labels=[];
    Data=[];

    GetCountTotal(){
        this._logService.GetCountTotal().subscribe(x=>{
            this.userCount=x[0].TotalUsers;
            this.logCount=x[0].TotalLogs;
            this.roleCount=x[0].TotalRoles;
        });
    }
    GetAllLog(){
        this._logService.GetLogOverview().subscribe(x=>{
            /*Process the LOG DATA */
            this.Logs=x;
            var openCount=0;
            var processCount=0;
            var doneCount=0;
            this.Logs.forEach(i => {
                if(i.Status=='Open'){
                    openCount++;
                }
                else if(i.Status=='Processing') {
                    processCount++;
                }
                else{
                    doneCount++;
                }
                /** LOG DATA */
                var dateString = new Date(i.CreateDate);
                var dateNow=new Date();
                if(dateString.getDate() == dateNow.getDate() 
                && dateString.getMonth() == dateNow.getMonth() 
                && dateString.getFullYear()==dateNow.getFullYear()){
                    this.newsCount++;
                }
                /** DAILY LOG */
                var label = dateString.getDate()+"/"+(dateString.getMonth()+1);
                var countLabel = 0;
                if(this.Labels.indexOf(label)==-1){
                    this.Labels.unshift(label);
                    this.Logs.forEach(j => {
                        var dateSubString=new Date(j.CreateDate);
                        var dateLabel = dateSubString.getDate()+"/"+(dateSubString.getMonth()+1);
                        if(dateLabel===label){
                            countLabel++;
                        }
                    });
                    this.Data.unshift(countLabel);
                }
                if(this.Labels.length>5){
                    this.Labels.slice(0,5);
                    this.Data.slice(0,5);
                }
                this.DrawChart();

            });
            var allLogs=openCount+processCount+doneCount;
            this.openPercent=(openCount*100)/allLogs;
            this.processPercent=(processCount*100)/allLogs;
            this.donePercent=(doneCount*100)/allLogs;
        });
    }

    CountPercentLogData(){
        
        

    }

    DrawChart(){
        var ctx = document.getElementById("myChart");//.getContext('2d');
        var Chart;
    var myChart = new Chart(ctx, {
        type: 'line',
        data: {
            labels: this.Labels,
            datasets: [{
                label: 'Daily Logs',
                data: this.Data,
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)'
                ],
                borderColor: [
                    'rgba(255,99,132,1)',
                ],
                borderWidth: 1
            }]
        },
        options: {
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero:true
                    }
                }]
            }
        }
    });
    }
    constructor(private _logService:LogService){
        
    }

    ngOnInit() {
        this.GetCountTotal();
        this.GetAllLog();
    }

}